/* Íµ¨Î∂ÑÏÑ† Ïä§ÌÉÄÏùº */
.separator {
  width: 1px;
  background-color: #ddd; /* Ïó∞Ìïú ÌöåÏÉâ */
  border-radius: 1px;
  opacity: 50%;
}

/* ÏûÖÎ†• Í∑∏Î£π */
.input-group {
  padding: 20px;
  display: flex;
  flex-direction: column;
}

/* ÎùºÎ≤® Ïä§ÌÉÄÏùº */
.input-group label {
  font-size: 16px;
  font-weight: 500;
  color: #282828;
  margin-bottom: 8px;
}

/* ÏûÖÎ†• ÌïÑÎìú Ïä§ÌÉÄÏùº */
.input-group input,
.input-group textarea {
  width: 100%;
  padding: 10px;
  border: 1px solid #dddddd;
  border-radius: 6px;
  font-size: 16px;
  color: #282828;
}

.input-group input:focus,
.input-group textarea:focus {
  border-color: #282828;
  outline: none;
}

/* textarea ÎÜíÏù¥ ÏÑ§Ï†ï */
.input-group textarea {
  height: 150px;
  resize: none;
}


.switch {
  position: relative;
  display: inline-block;
  width: 40px;
  height: 20px;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: 0.4s;
  border-radius: 20px;
}

.slider:before {
  position: absolute;
  content: "";
  height: 14px;
  width: 14px;
  left: 3px;
  bottom: 3px;
  background-color: white;
  transition: 0.4s;
  border-radius: 50%;
}

input:checked + .slider {
  background-color: #282828;
}

input:checked + .slider:before {
  transform: translateX(20px);
}

/* Ï†úÏ∂ú Î≤ÑÌäºÏùÑ Ìèº ÎÇ¥Î∂ÄÏóêÏÑú ÏïÑÎûòÏ™ΩÏóê Í≥†Ï†ï */
