.comment {
    display: flex;
    justify-content: space-between; /* ÎåìÍ∏Ä ÎÇ¥Ïö©Í≥º Î≤ÑÌäºÏùÑ Ï¢åÏö∞Î°ú Ï†ïÎ†¨ */
    align-items: center; /* ÏàòÏßÅ Ï§ëÏïô Ï†ïÎ†¨ */
    border-bottom: 1px solid #ddd;
    padding: 12px 0;
  }
  
  .comment-content-wrapper {
    display: flex;
    flex-direction: column;
  }
  
  .comment-header {
    display: flex;
    gap: 10px; /* ÎãâÎÑ§ÏûÑÍ≥º ÎÇ†Ïßú ÏÇ¨Ïù¥ Í∞ÑÍ≤© */
    align-items: center;
    font-size: 14px;
    margin-bottom: 5px;
  }
  
  .comment-header strong {
    font-size: 16px;
    font-weight: 500;
    color: #282828;
  }
  
  .comment-date {
    font-size: 16px;
    font-weight: 400;
    color: #B8B8B8;
  }
  
  .comment-content {
    font-size: 16px;
    color: #282828;
    margin: 10px 0 0;
  }
  
  .comment-footer {
    display: flex;
    gap: 20px;
    align-items: center; /* ÏàòÏßÅ Ï§ëÏïô Ï†ïÎ†¨ */
  }
  
  .comment-footer button {
    background: none;
    border: none;
    cursor: pointer;
  }
  
  .comment-footer img {
    width: 20px;
    height: 20px;
  }
  