/* Î™®Îã¨ Ïò§Î≤ÑÎ†àÏù¥ */
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
}

/* Î™®Îã¨ Ïª®ÌÖêÏ∏† - ÌÅ¨Í∏∞ Ï†úÌïú Ï†úÍ±∞ */
.modal-content {
  width: 80vw;  /* Î∑∞Ìè¨Ìä∏ ÎÑàÎπÑÏùò 80% */
  max-width: 480px; /* ÎÑàÎ¨¥ Ïª§ÏßÄÎäî Í±∏ Î∞©ÏßÄ */
  height: auto;  /* ÎÜíÏù¥Î•º ÎÇ¥Ïö©Ïóê ÎßûÍ≤å ÏûêÎèô Ï°∞Ï†à */
  max-height: 90vh; /* ÌôîÎ©¥ÏùÑ ÎÑòÏßÄ ÏïäÎèÑÎ°ù ÏÑ§Ï†ï */
  background: white;
  border-radius: 10px;
  padding: 40px;
  position: relative;
  display: flex;
  flex-direction: column;
}


/* Îã´Í∏∞ Î≤ÑÌäº */
.close-button {
  position: absolute;
  top: 20px;
  right: 20px;
  font-size: 24px;
  background: none;
  border: none;
  cursor: pointer;
}

/* Ï†úÎ™© Ïä§ÌÉÄÏùº */
h2 {
  text-align: center;
  font-size: 24px;
  font-weight: 700;
  color: #282828;
  margin-bottom: 20px;
}

.modal-form {
  display: flex;
  flex-direction: column; /* ÏÑ∏Î°ú Î∞©Ìñ• Ï†ïÎ†¨ */
  justify-content: center;
  align-items: center;
  gap: 20px;
  width: 100%;
}
  

/* ÏûÖÎ†• Í∑∏Î£π */
.input-group {
  padding: 20px;
  max-width: 400px;
  width: 100%;
  display: flex;
  flex-direction: column;
}

/* ÎùºÎ≤® Ïä§ÌÉÄÏùº */
.input-group label {
  font-size: 16px;
  font-weight: 500;
  color: #282828;
  margin-bottom: 8px;
}

/* ÏûÖÎ†• ÌïÑÎìú Ïä§ÌÉÄÏùº */
.input-group input {
  width: 100%;
  padding: 10px;
  border: 1px solid #dddddd;
  border-radius: 6px;
  font-size: 16px;
  color: #282828;
}

  
/* Ï†úÏ∂ú Î≤ÑÌäº Ïä§ÌÉÄÏùº */
.submit-button,
.comment-delete-button,
.memory-delete-button {
  max-width: 400px;
  width: 100%;
  height: 50px;
  border-radius: 6px;
  background-color: #282828;
  color: #fafafa;
  font-size: 16px;
  font-weight: 500;
  border: none;
  cursor: pointer;
  align-self: center;
  margin-top: 20px;
}

.submit-button:hover {
  background-color: #1f1f1f;
}

.input-group input.error {
  border-color: red;
}