}

.edit-button,
.delete-button {
  padding: 8px 12px;
  border: solid #ccc;
  background-color: white;
  cursor: pointer;
  border-radius: 5px;
}

.edit-button:hover,
.delete-button:hover {
  background-color: #f0f0f0;
}

/* ÍΩÉ Ïù¥ÎØ∏ÏßÄ & Í≥µÍ∞ê Î≤ÑÌäº */
.flower-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 8px;
}

.flower-img {
  width: 80px;
  height: auto;
}

.like-button {
  background: none;
  border: none;
  cursor: pointer;
  padding: 0;
  width: 120px;
  height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.like-button img {
  width: 100%;
  height: auto;
  object-fit: contain;
  transition: transform 0.2s ease-in-out;
}

.like-button img:hover {
  transform: scale(1.1);
}

.header {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 20px 0;
}

.logo {
  height: 40px; /* ÌïÑÏöîÏóê Îî∞Îùº ÌÅ¨Í∏∞ Ï°∞Ï†à */
