.memory-header {
  display: flex;
  flex-direction: column; /* Ï†úÎ™©ÏùÑ ÏúÑÏóê, Î≤ÑÌäºÏùÑ ÏïÑÎûò Î∞∞Ïπò */
  align-items: center;
  margin-bottom: 20px;
  white-space: nowrap; /* Ï§ÑÎ∞îÍøà Î∞©ÏßÄ */
}
.memory-info {
  display: flex;
  align-items: center;
  gap: 8px; /* ÎãâÎÑ§ÏûÑÍ≥º Í≥µÍ∞ú ÏÉÅÌÉú ÏÇ¨Ïù¥ Í∞ÑÍ≤© */
  font-size: 16px;
}

.nickname {
  font-weight: bold;
}

.divider {
  font-weight: bold;
}

.visibility {
  color: gray;
}

.title {
  font-size: 24px;
  font-weight: bold;
  margin-bottom: 15px;
  text-align: center;
}

/* Î≤ÑÌäº Î∞è Í≤ÄÏÉâÏ∞ΩÏùÑ Í∞ÄÎ°ú Ï†ïÎ†¨ */
.controls {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 15px; /* Î≤ÑÌäº Í∞ÑÍ≤© */
  width: 100%;
  max-width: 900px;
}

.controls button {
  background-color: #fff;
  border: 2px solid #000;
  color: #000;
  padding: 8px 15px;
  border-radius: 20px;
  cursor: pointer;
  font-weight: bold;
}

.controls button.active {
  background-color: #000;
  color: white;
}

.search-container {
  flex-grow: 1;
  position: relative;
}

.search-container input {
  width: 100%;
  padding: 10px;
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid #ccc;
}

.search-container input::placeholder {
  color: #aaa;
}

.sort-container {
  display: flex;
  align-items: center;
}

.sort-container select {
  padding: 8px;
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid #ccc;
  margin-left: 10px;
}

.upload-btn {
  background-color: black;
  color: white;
  border: none;
  padding: 10px 15px;
  cursor: pointer;
  font-size: 14px;
  border-radius: 5px;
  margin-left: 10px;
}

.upload-btn:hover {
  background-color: #333;
}

/* üîπ Ï∂îÏñµ ÏóÜÏùå ÏÑπÏÖò */
.empty-memory {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  margin-top: 10px;
}

.block-img {
  width: 80px;
  height: 80px;
  margin-bottom: 8px;
}

.empty-text {
  font-size: 16px;
  font-weight: bold;
  color: #555;
}

.empty-subtext {
  font-size: 14px;
  color: #777;
}
